.edavisualiser-root .hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #011627;
  color: #d6deeb;
}

/* General Purpose */

.edavisualiser-root .hljs-keyword {
  color: #c792ea;
  font-style: italic;
}

.edavisualiser-root .hljs-built_in {
  color: #addb67;
  font-style: italic;
}

.edavisualiser-root .hljs-type {
  color: #82aaff;
}

.edavisualiser-root .hljs-literal {
  color: #ff5874;
}

.edavisualiser-root .hljs-number {
  color: #F78C6C;
}

.edavisualiser-root .hljs-regexp {
  color: #5ca7e4;
}

.edavisualiser-root .hljs-string {
  color: #ecc48d;
}

.edavisualiser-root .hljs-subst {
  color: #d3423e;
}

.edavisualiser-root .hljs-symbol {
  color: #82aaff;
}

.edavisualiser-root .hljs-class {
  color: #ffcb8b;
}

.edavisualiser-root .hljs-function {
  color: #82AAFF;
}

.edavisualiser-root .hljs-title {
  color: #DCDCAA;
  font-style: italic;
}

.edavisualiser-root .hljs-params {
  color: #7fdbca;
}

/* Meta */

.edavisualiser-root .hljs-comment {
  color: #637777;
  font-style: italic;
}

.edavisualiser-root .hljs-doctag {
  color: #7fdbca;
}

.edavisualiser-root .hljs-meta {
  color: #82aaff;
}

.edavisualiser-root .hljs-meta-keyword {
  color: #82aaff;
}

.edavisualiser-root .hljs-meta-string {
  color: #ecc48d;
}

/* Tags, attributes, config */

.edavisualiser-root .hljs-section {
  color: #82b1ff;
}

.edavisualiser-root .hljs-tag,
.edavisualiser-root .hljs-name,
.edavisualiser-root .hljs-builtin-name {
  color: #7fdbca;
}

.edavisualiser-root .hljs-attr {
  color: #7fdbca;
}

.edavisualiser-root .hljs-attribute {
  color: #80cbc4;
}

.edavisualiser-root .hljs-variable {
  color: #addb67;
}

/* Markup */

.edavisualiser-root .hljs-bullet {
  color: #d9f5dd;
}

.edavisualiser-root .hljs-code {
  color: #80CBC4;
}

.edavisualiser-root .hljs-emphasis {
  color: #c792ea;
  font-style: italic;
}

.edavisualiser-root .hljs-strong {
  color: #addb67;
  font-weight: bold;
}

.edavisualiser-root .hljs-formula {
  color: #c792ea;
}

.edavisualiser-root .hljs-link {
  color: #ff869a;
}

.edavisualiser-root .hljs-quote {
  color: #697098;
  font-style: italic;
}

/* CSS */

.edavisualiser-root .hljs-selector-tag {
  color: #ff6363;
}

.edavisualiser-root .hljs-selector-id {
  color: #fad430;
}

.edavisualiser-root .hljs-selector-class {
  color: #addb67;
  font-style: italic;
}

.edavisualiser-root .hljs-selector-attr,
.edavisualiser-root .hljs-selector-pseudo {
  color: #c792ea;
  font-style: italic;
}

/* Templates */

.edavisualiser-root .hljs-template-tag {
  color: #c792ea;
}

.edavisualiser-root .hljs-template-variable {
  color: #addb67;
}

/* diff */

.edavisualiser-root .hljs-addition {
  color: #addb67ff;
  font-style: italic;
}

.edavisualiser-root .hljs-deletion {
  color: #EF535090;
  font-style: italic;
}

